.language {
  display: flex;
  align-items: center;
  padding: 4px 6px;
  border-radius: 5px;
  cursor: pointer; }
  .language__text {
    display: block;
    color: var(--white);
    font-size: 14px;
    line-height: 1;
    text-transform: uppercase; }

.language-list {
  position: relative;
  background-color: var(--gray-800);
  border-radius: 5px;
  box-shadow: 0 1px 5.5px 0.5px rgba(var(--black), 0.08); }
  .language-list::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    width: 0;
    height: 0;
    border-top: 5px solid var(--white);
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    transform: translateX(-50%); }
  .language-list:hover {
    background-color: rgba(var(--gray-100), 0.5); }
    .language-list:hover::before {
      border-top: 5px solid rgba(var(--gray-100), 0.5); }

.language-selector {
  position: relative;
  margin-right: 18px; }
  .language-selector__current {
    border: 1px solid var(--white);
    border-radius: 5px;
    overflow: hidden; }
  .language-selector .language-list {
    position: absolute;
    bottom: calc(100% - 1px);
    left: 0;
    transform-origin: bottom center;
    transform: scaleY(0);
    transition: transform 0.2s ease; }
  .language-selector::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 50%;
    right: -16px;
    width: 8px;
    height: 8px;
    border-right: 2px solid var(--white);
    border-bottom: 2px solid var(--white);
    transform: translateY(-70%) rotate(45deg); }
  .language-selector_opened .language-list {
    transform: scaleY(1); }

