  position: relative;
  margin-top: var(--mt);
  padding-top: var(--pt);
  background-color: var(--gray-700);
  border-radius: 20px;
  color: var(--primary);
  transition: padding-top 0.3s, background-color 0.3s, color 0.3s, margin-top 0.3s; }
  .links-swiper-slide__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 65px 22px 35px; }
  .links-swiper-slide__icon {
